<template>
  <div class="hello">
    <div id='container'>
      <!--
                1  获取当前位置的经纬度
                2  将经纬度返回给后端获取当前位置周边的设备
                3  渲染数据到到列表
                4  点击列表item计算当前位置到该设备的步行路线
            -->
    </div>
      <div class="backbtnBox" v-show="showPanel">
        <p @click='showback()'>返回</p>
        <div class="penalBox">
            <i @click='penaldown()' class="lock lock-down" v-if='checkpanel'></i>
            <i @click='penalup()' class="lock lock-up" v-else></i>
        </div>
      </div>
      <div id="panel" ref="panel" v-show="showPanel"></div>
    <!-- <div class="btn-refresh" @click="refreshFn" ref="refresh">
      <img src="../assets/refresh-btn.png" alt="">
    </div> -->
    <zx-popup v-model="showBoolean" position="bottom" height="100%">
      <zx-cell-group>
        <div v-if='positionData.length!=0'>
          <zx-cell-item v-for="(item,index) of positionData" class="newStyle" key="item">
            <div class="deviceLeft">
              <p>{{item.alias}}</p>
              <p>{{item.address.address}}</p>
            </div>
            <div class="deviceMid">
              <p>{{item.default_pay}}元起</p>
              <p>{{item.distance}}m</p>
            </div>
            <div class="deviceRight" @click="showPath(item.address)">
              <p>到这去</p>
            </div>
          </zx-cell-item>
      </div>
      <div v-else>
        <div class="noDevice">周边暂无设备</div>
      </div>
      </zx-cell-group>
    </zx-popup>
  </div>
</template>
<script>
  import dataURL from "../service/dataURL.js"
  import Ajax from "../api/ajax.js"
  export default {
    name: 'hello',
    data() {
      return {
        checkpanel:true,
        positionData: [],
        defaultPosition: [],
        showBoolean: false,
        showPanel: false
      }
    },
    methods: {
      showback(){
        this.showBoolean = true;
        this.showPanel = false;
      },
      penaldown(){
        this.checkpanel = false;
        let doc = document.getElementById('panel');
        let docBtn = document.getElementsByClassName('backbtnBox')[0];
        doc.style.height = '0rem';
        docBtn.style.bottom = 0;
        doc.style.transition = '.3s';
        docBtn.style.transition = '.3s';
      },
      penalup(){
        this.checkpanel = true;
        let doc = document.getElementById('panel');
        let docBtn = document.getElementsByClassName('backbtnBox')[0];
        doc.style.height = '40%';
        docBtn.style.bottom = '40%';
        doc.style.transition = '.3s';
        docBtn.style.transition = '.3s';
      },
      //计算椭圆距离
      distanceCula(lat1,lng1,lat2,lng2){
        var radLat1 = this.distanceLine(lat1);
        var radLat2 = this.distanceLine(lat2);
        var a = radLat1 - radLat2;
        var  b = this.distanceLine(lng1) - this.distanceLine(lng2);
        var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a/2),2) +
        Math.cos(radLat1)*Math.cos(radLat2)*Math.pow(Math.sin(b/2),2)));
        s = s *6378.137 ;// EARTH_RADIUS;
        s = Math.round(s * 10000) / 10; //输出为m
        s=s.toFixed(1);
        return s;
      },
      //进行经纬度转换为距离的计算
      distanceLine(d){
        return d * Math.PI / 180.0;//经纬度转换成三角函数中度分表形式。
      },
      refreshFn() {
        // 如果设备经纬度数组的长度为0
        if (this.positionData.length <= 0) return false;
        var refreshlEle = this.$refs.refresh;
        refreshlEle.classList.toggle("refresh-rotate")
        this.showBoolean = true;
        this.showPanel = false;
      },
      async create() {
        let _this = this;
        var map, geolocation, defaultPosition;
        map = new AMap.Map('container', {
          resizeEnable: true,
          zoom: 13,
        });
        // 获取定位
        map.plugin('AMap.Geolocation', function () {
          geolocation = new AMap.Geolocation({
            enableHighAccuracy: true,
            timeout: 10000,
            buttonOffset: new AMap.Pixel(10, 20),
            zoomToAccuracy: true,
            buttonPosition: 'RB'
          });
          map.addControl(geolocation);
          geolocation.getCurrentPosition();
          // 定位成功
          AMap.event.addListener(geolocation, 'complete', async (data) => {
            defaultPosition = [data.position.getLng(), data.position.getLat()];
            _this.getDeviceData(data.position.getLng(), data.position.getLat(),map);
          });
          // 定位失败
          AMap.event.addListener(geolocation, 'error', (data) => {
            alert('定位失败');
            defaultPosition = [];
          });
        });
      },
      //获取路径 
      showPath(item) {
        var panelEle = this.$refs.panel;
        panelEle.innerHTML = '';
        var map;
        map = new AMap.Map('container', {
          resizeEnable: true,
          zoom: 13,
        });
        var walking = new AMap.Walking({
          map: map,
          panel: "panel",
        });
        //根据起终点坐标规划步行路线
        this.showPanel = true;
        this.showBoolean = false;
        walking.search(this.defaultPosition, [item.longitude, item.latitude]);
      },
      // 获取设备的数据
      async getDeviceData(longitude, latitude,maps) {
        this.defaultPosition = [longitude, latitude];
        console.log(this.defaultPosition);
        // try {
          let res = await Ajax.Post(dataURL.nearDevice, {
            longitude: longitude,
            latitude: latitude,
          });
          console.log(res);
          if (!res.length || res.length < 0) {
            alert('周边暂无设备');
            return;
          }else{
            res.forEach(element => {
              element.distance = ''; 
              element.distance=this.distanceCula(latitude,longitude,element.address.latitude,element.address.longitude);
              element.default_pay = (parseFloat(element.default_pay)/100).toFixed(2);
              if(element.address!=null||element.address!=''){
                element.address.address = element.address.address.replace(element.address.province,'');
                element.address.address = element.address.address.replace(element.address.city,'');
                element.address.address = element.address.address.replace(element.address.county,'');
                //添加点标记，并使用自己的icon
                var marker=new AMap.Marker({
                    map: maps,
                        position: [element.address.longitude, element.address.latitude],
                        icon: new AMap.Icon({            
                        size: new AMap.Size(35, 44),  //图标大小
                        image: "http://webapi.amap.com/theme/v1.3/images/newpc/poi-1.png",
                        imageOffset: new AMap.Pixel(-115,-90)
                        })        
                    });
                    marker.setLabel({//label默认蓝框白底左上角显示，样式className为：amap-marker-label
                            offset: new AMap.Pixel(-60, -30),//修改label相对于maker的位置
                            content: ''
                        });
                    marker.on("click", function(e) {
                      if(marker.Qi.label.content==''){
                        marker.setLabel({//label默认蓝框白底左上角显示，样式className为：amap-marker-label
                            offset: new AMap.Pixel(-60, -30),//修改label相对于maker的位置
                            content: element.alias+"</br>地址："+element.address.address
                        });
                      }else{
                        marker.setLabel({//label默认蓝框白底左上角显示，样式className为：amap-marker-label
                            offset: new AMap.Pixel(-60, -30),//修改label相对于maker的位置
                            content: ''
                        });
                      }
                    });
              }
            });
          }
          this.showBoolean = true;
          this.positionData = res;
        // } catch (error) {
        //   console.log("Ajax dataURL.nearDevice Error")
        // }
      }
    },
    mounted() {
      this.create();
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  @import '../assets/css/mains.css';
</style>


