import Axios from 'axios'
import Config from '../config/axios.config.js'
import Future from "./future.js";
// import outWeb from "../../build/outWebAddress.js"
// 上传文件的配置fileUploadConfig
var fileUploadConfig = {
    baseURL: "https://api.test2.gzmypay.com/",//线上请求地址
    timeout: 50000,
    headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        'token': window.localStorage.getItem('device_token') ? window.localStorage.getItem('device_token') : ''
    }
}
var LoadingInstance = {};
// 数据请求之前
Axios.interceptors.request.use((config) => {
    var token = window.localStorage.getItem('device_token');
    if (token !== undefined && token !== 'undefined' && token !== '' && token !== null) {
        config.headers.token = token;
    } else {
        console.info('device_token not found');
    }
    return config;
});
// 提交成功并返回数据时
Axios.interceptors.response.use(function (response) {
    return response;
}, function (error) {
    return Promise.reject(error);
});

class Ajax {
    static ResponseHandle(F, res) {
        switch (res.data.Error) {
            case 1:
                Notification({
                    title: '请求提示',
                    message: res.data.Msg,
                    type: 'warning'
                });
                F.reject(res.data.Msg);
                break;
            case 2:
                window.localStorage.user_status = 'platform_admin_pc_outlogin';
                // App.$router.push('/login');
                break;
            default:
                F.resolve(res.data.Data);
                break;
        }
    }
    static Post(url, data) {
        var F = new Future();
        Axios.post(url, data, Config)
            .then((res) => {
                Ajax.ResponseHandle(F, res);
            })
            .catch((e) => {
                F.reject(e);
            });
        return F;
    }
    static upLoadFile(url, data) {
        var F = new Future();
        Axios.post(url, data, fileUploadConfig)
            .then((res) => {
                Ajax.ResponseHandle(F, res);
            })
            .catch((e) => {
                console.info(e);
                F.reject(e);
            });
        return F;
    }
    static Get(url) {
        var F = new Future();
        Axios.get(url, Config)
            .then((res) => {
                Ajax.ResponseHandle(F, res);
            })
            .catch((e) => {
                F.reject(e);
            });
        return F;
    }
}

export default Ajax;


