<template>
    <transition :name="checkName()">
        <div class="popup-group clearfixed" v-show="show" ref="popup">
                <!-- <div class="popup-mask" @click.stop="close"></div> -->
                <div class='nearClass'>
                    <h2>附近设备</h2>
                    <i @click='down()' class="lock lock-down" v-if='locked'></i>
                    <i @click='up()' class="lock lock-up" v-else></i>
                </div>
                <div :style="style()" :class='checkClass()'>
                        <slot></slot>
                </div>
        </div>
    </transition>
</template>
<script>
    export default {
        name: "zx-popup",
        props: {
            value: {
                type: Boolean,
                default: false
            },
            width: {
                type: String,
                default: '60%'
            },
            height: {
                type: String,
                default: '60%'
            },
            position: {
                type: String,
                default: "right"
            }
        },
        data() {
            return {
                show: this.value,
                locked:true
            }
        },
        watch: {
            value(val) {
                if (val) {
                    this.$refs["popup"].className = "popup-group popup-show"
                } else {
                    this.$refs["popup"].className = "popup-group"
                }
                this.show = val;
            }
        },
        methods: {
            down(){
                this.locked = false;
                let doc = document.getElementsByClassName('popup-group')[0];
                doc.style.height = '3.2rem';
                doc.style.transition = '.3s';
            },
            up(){
                this.locked = true;   
                let doc = document.getElementsByClassName('popup-group')[0];
                doc.style.height = '55%';
                doc.style.transition = '.3s';                        
            },
            click($event) {
                this.$emit("click", $event)
            },
            style() {
                if (this.position == "right" || this.position == "left") {
                    return { width: this.width }
                } else {
                    return { height: this.height }
                }
            },
            checkClass() {
                return 'popup-cont-' + this.position;
            },
            close() {
                this.show = false;
                this.$emit('input', false);
            },
            checkName() {
                return "popup-" + this.position;
            }
        },
        created() {

        }
    }

</script>
<style>
    /* 可以设置不同的进入和离开动画 */
    /* 设置持续时间和动画函数 */
    .nearClass h2{
        float: left;
    }
    .lock{
        width: 1.5rem;
        height: .7rem;
        display: inline-block;
        background-size: 100% 100%!important;
        background-repeat:no-repeat;
        float: right;
        margin-right: 2rem;
        margin-top: .3rem;
    }
    .lock-down{
        background-image: url('../../../../assets/down.png');
    }
    .lock-up{
        background-image: url('../../../../assets/up.png');
    }
    .popup-right-enter-active,
    .popup-right-leave-active,
    .popup-top-enter-active,
    .popup-top-leave-active,
    .popup-bottom-enter-active,
    .popup-bottom-leave-active,
    .popup-left-enter-active,
    .popup-left-leave-active {
        transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .popup-right-enter,
    .popup-right-leave-active {
        transform: translateX(10px);
        opacity: 0;
    }

    .popup-left-enter,
    .popup-left-leave-active {
        transform: translateX(-10px);
        opacity: 0;
    }

    .popup-bottom-enter,
    .popup-bottom-leave-active {
        transform: translateY(10px);
        opacity: 0;
    }

    .popup-top-enter,
    .popup-top-leave-active {
        transform: translateY(-10px);
        opacity: 0;
    }
    .nearClass{
        text-align: left;
        padding: 1rem;
        /* position: relative; */
        margin: 0;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999999;
        width: 100%;
        background-color: #fff;
    }
    .nearClass>h2{
        font-size: 1rem;
        font-weight: normal;
        padding: 0;
        margin: 0;
    }
    .nearClass::after{
        display: block;
            content: "";
            position: absolute;
            left: inherit;
            right: 0;
            bottom: 0;
            left:0;
            height: 1px;
            width: 100%;
            background: #e4e4e4;
            transform: scaleY(0.5);
    }
</style>


