// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import "./components/public/zxhuan.css";
import "./assets/map/map-main.css";
import "./assets/map/liteToolbar.js";
import Zxhuan from "./components/public/zxhuan.js";
Vue.use(Vuex);
Vue.config.productionTip = false
Vue.use(Zxhuan);
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



